document.addEventListener('DOMContentLoaded', function() {
    eventListeners();
    darkMode();
    borraMensaje();
});
function borraMensaje() {
    const mensajeConfirm = document.querySelector('.eliminar-anuncio');
    if(mensajeConfirm){
        setTimeout(function() {
            const padre = mensajeConfirm.parentElement;
            padre.removeChild(mensajeConfirm);
        }, 3500);
    }
}
function darkMode() {
    const botonDarkMode = document.querySelector('.dark-mode-boton');
    const prefiereDarMode = window.matchMedia('(prefers-color-scheme: dark)');
    // console.log(prefiereDarMode.matches);
    if(prefiereDarMode.matches) {
        document.body.classList.add('dark-mode');
    }else {
        document.body.classList.remove('dark-mode');
    }
    prefiereDarMode.addEventListener('change', function() {
        if(prefiereDarMode.matches) {
            document.body.classList.add('dark-mode');
        }else {
            document.body.classList.remove('dark-mode');
        }
    });
    botonDarkMode.addEventListener('click', function() {
        document.body.classList.toggle('dark-mode');
    });
}

function eventListeners() {
    const mobileMenu = document.querySelector('.mobile-menu');
    mobileMenu.addEventListener('click', navegacionResponsive);

    // Muestra campos condicionales
    const metodoContacto =  document.querySelectorAll('input[name="contacto[contacto]"]')
    metodoContacto.forEach(input => input.addEventListener('change', mostrarMetodoContacto));
}
function navegacionResponsive() {
    const navegacion = document.querySelector('.navegacion');
    // if(navegacion.classList.contains('mostrar')) {
    //     navegacion.classList.remove('mostrar');
    // } else {
    //     navegacion.classList.add('mostrar');
    // }

    navegacion.classList.toggle('mostrar'); // hace lo mismo que el if de arruba
}

function mostrarMetodoContacto(e) {
    const contactoDiv = document.querySelector('#contacto');
    if (e.target.value === "telefono") {
        texto = `
            <label for="telefono">N&uacute;mero Tel&eacute;fono</label>
            <input type="tel" placeholder="Tu Tel&eacute;fono" id="telefono" name="contacto[telefono]" required>
            <p>Elija la fecha y la hora para la llamada</p>
            <label for="fecha">Fecha:</label>
            <input type="date" id="fecha" name="contacto[fecha]" required>
            <label for="hora">Hora:</label>
            <input type="time" id="hora" min="09:00" max="18:00" name="contacto[hora]" required>
        `;
        transicionContacto(contactoDiv, texto);
    } else {
        texto = `
            <label for="email">E-mail</label>
            <input type="email" placeholder="Tu Email" id="email" name="contacto[email]" required>
        `;
        transicionContacto(contactoDiv, texto);
    }
    

}

function transicionContacto(contacto, texto) {
    if(!contacto.classList.contains('mostrarContacto')) {
        contacto.classList.add('mostrarContacto');
        contacto.innerHTML =texto
    }else {
        contacto.classList.remove('mostrarContacto');
        setTimeout(function() {
            contacto.classList.add('mostrarContacto');
            contacto.innerHTML =texto
        }, 600);  
    }
    
}